<template>
  <div class="charge-page">
    <div class="charge-header">
      <div class="d-flex header-contents">
        <div class="greeting">
          <p style="font-family: 'NicoMoji' !important;">DABO </p>
        </div>
      </div>
    </div>
    <div class="payInfo">
      <button style="font-weight: bold;">DABO </button>&nbsp;
      <button  style="color: #f08986">{{ this.selectDabo*10000 }} DABO</button>
      <br>
      <button style="font-weight: bold;"> </button>&nbsp;
      <button  style="color: #f08986">{{ this.selectDabo*10000 }} </button>
    </div>
  
    <div class="payable">
    <p style="font-weight: bold;">  </p>
      <div class="myBtn">
        <a href="#bDonation"><button> </button></a>
        <button @click="toBack()"></button>
      </div>
    </div>
