      <div id="bDonation" class="modal-window">
          <div>
            <p v-if="!isCashCharging">DABO   </p>
              <div class="spinner" style="text-align: center; !important" v-if="isCashCharging">
                <p>   30 .</p>
                <v-progress-circular
                  :size="70"
                  color="#f06464"
                  indeterminate
                ></v-progress-circular>
              </div>
            <sub v-if="!isCashCharging">
                <b-icon icon="exclamation-circle" style="width: 10px; height: 10px;"></b-icon>
                DABO         .
                     ,         .
                <p></p>
            </sub>
            <div class="input-text" v-if="!isCashCharging">
              <input type="text" v-model="privateKey" placeholder="private key ">
            </div>
            <div v-if="!isCashCharging">
              <a href="#">
                <button class="btn_red_cancel">
                    <span></span>
                </button>
              </a>
              <button class="btn_red_modal" @click="checkPay()">
                  <span></span>
              </button>
            </div>
          </div>
          
      </div>

    </div>
  </div>
</template>

<script>
import { getUserInfo } from "@/api/user.js";
import * as walletService from "@/api/wallet.js";
import { createWeb3 } from "@/utils/web3.js";
import { buyCash, getBalance } from "@/utils/cashContract.js";
import { ethToWei } from "@/utils/ethereumUnitUtils.js";
import BN from "bn.js";
export default {
  data() {
    return {
      selectDabo: 0,
      
      wallet: {
        id: 0,
        ownerId: null,
        address: "",
        balance: 0,
        payBalance : 0,
        cash: 0,
        receivingCount: 0,
      },
      isCharging: false, //      
      isCashCharging: false, //      
      cashChargeAmount: 0.1,
      userId: this.$store.state.user.id,
      walletAddress: this.$store.state.user.walletAddress,
      privateKey: null,
    }
  },
  methods: {
    checkPay() {
       /* 1.   */
      const { IMP } = window;
      IMP.init('imp00423345'); // test  

      /* 2.    */
      const data = {
        pg: 'html5_inicis',                           // PG
        pay_method: 'card',                           // 
        merchant_uid: `mid_${new Date().getTime()}`,   // 
        amount: this.selectDabo*10000,                                 // 
        name: '   ',                  // 
        buyer_name: '',                           //  
        buyer_tel: '01012341234',                     //  
        buyer_email: 'example@example',               //  
        buyer_addr: ' 661-16',                    //  
        buyer_postcode: '06018',                      //  
      };

      /* 4.    */
      IMP.request_pay(data, this.callback);
    },
    callback(response) {
      /* 3.    */
      const {
        success,
        error_msg,
        
      } = response;

      if (success) {
        alert(' ');
        
      } else {
        alert(` : ${error_msg}`);
        this.chargeCash()
      }
    },
    chargeCash() {
      const vm = this;
      this.isCashCharging = true;
      // const privateKey = this.privateKey;
      const privateKey = vm.privateKey;
      if (privateKey) {
        /**
         *    
         */
        buyCash(
          new BN(ethToWei(String(this.cashChargeAmount))),
          privateKey,
          function() {
            alert(" .");
            vm.isCashCharging = false;
            vm.fetchCashBalance();
            vm.$router.go({ path: 'dabowallet/chargeconfirm'})
          },
          function() {
            alert("  .");
            vm.isCashCharging = false;
          }
        );
      }
    },
    fetchCashBalance() {
      const vm = this;
      getBalance(
        this.walletAddress,
        function(balance) {
          vm.wallet.cash = balance;
        },
        function(err) {
          console.error("   :", err);
        }
      );
    },
    fetchEtherBalance() {
      const vm = this;
      walletService.findByUserId(this.userId, function(res) {
        const web3 = createWeb3();
        vm.wallet.balance = web3.utils.fromWei(
          res.data.balance.toString(),
          "ether"
        );
      });
    },
    fetchUserInfo(){
      // const vm = this;
      getUserInfo(
            function (response) {
              console.log("getUserInfo",response);
              // vm.user.email = response.data.email;
              // vm.user.nickname = response.data.nickname;
            },
            function (err) {
              if (err.response != 404) {
                console.error(err);
                // alert("   .");
              }
            }
          );
    },
    fetchWalletInfo() {
      const vm = this;

      walletService.findByUserId(this.userId, function(response) {
        const data = response.data;
        const web3 = createWeb3();
        data["balance"] = web3.utils.fromWei(
          data["balance"].toString(),
          "ether"
        );
        vm.wallet = data;
      });
    },
    toBack() {
      this.$router.push({name: 'daboWallet'})
    }
  },
  mounted() {
    this.fetchWalletInfo();
    // this.fetchUserInfo();
  },
  created() {
    this.selectDabo = this.$route.params.selected
  }
}
</script>

