  },
  methods: {
    checkPay() {
       /* 1.   */
      const { IMP } = window;
      IMP.init('imp00423345'); // test  

      /* 2.    */
      const data = {
        pg: 'html5_inicis',                           // PG
        pay_method: 'card',                           // 
        merchant_uid: `mid_${new Date().getTime()}`,   // 
        amount: 1000,                                 // 
        name: '   ',                  // 
        buyer_name: '',                           //  
        buyer_tel: '01012341234',                     //  
        buyer_email: 'example@example',               //  
        buyer_addr: ' 661-16',                    //  
        buyer_postcode: '06018',                      //  
        
      };

      /* 4.    */
      IMP.request_pay(data, this.callback);
    },
    callback(response) {
      /* 3.    */
      const {
        success,
        error_msg,
        
      } = response;

      if (success) {
        alert(' ');
      } else {
        alert(` : ${error_msg}`);
      }

      

      this.$router.push({name: 'chargeConfirm', params: ''})
    },
    chargeCash() {
      const vm = this;
      this.isCashCharging = true;
      const privateKey = prompt("   .");
      if (privateKey) {
        /**
         *    
         */
        buyCash(
          new BN(ethToWei(String(this.cashChargeAmount))),
          privateKey,
          function() {
            alert(" .");
            vm.isCashCharging = false;
            vm.fetchWalletInfo();
            vm.fetchCashBalance();

          },
          function() {
            alert("  .");
            vm.isCashCharging = false;
          }
        );
      }
    },
    fetchCashBalance() {
      const vm = this;
      getBalance(
        this.walletAddress,
        function(balance) {
          vm.wallet.cash = balance;
        },
        function(err) {
          console.error("   :", err);
        }
      );
    },
    fetchWalletInfo() {
      const vm = this;
      walletService.findByUserId(this.userId, function(response) {
        const data = response.data;
        const web3 = createWeb3();
        data["balance"] = web3.utils.fromWei(
          data["balance"].toString(),
          "ether"
        );
        vm.wallet = data;
      });
    },
